@import url(https://fonts.googleapis.com/css2?family=Inconsolata);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body{margin:0;font-family:Inconsolata,monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body{height:100%;width:100%;scroll-behavior:smooth}#root{display:flex;height:100%;flex-direction:column;align-items:center;justify-content:space-between;--bg-color: black;--font-color: lightslategray;--hl-color: orange;--fg-color: #292f34;color:var(--font-color);background-color:var(--bg-color)}button{background-color:var(--bg-color);color:var(--font-color);border:2px solid var(--font-color);padding:10px;font-size:21pt;font-family:Inconsolata,monospace;font-weight:800;margin:10px;cursor:pointer}button:hover{color:var(--fg-color);border-color:2px solid var(--fg-color)}a{color:var(--font-color);text-decoration:none;transition:color .1s ease}a span{font-size:smaller;margin:2.5px}a:hover{color:var(--fg-color)}.hidden{opacity:0;pointer-events:none}
table{font-size:16pt;font-weight:800}
.test{display:flex;flex-direction:column;font-size:21pt;font-weight:700;width:50%}.timer{margin:5px;color:var(--fg-color);cursor:pointer}.box{overflow:hidden;height:93px;display:flex;flex-wrap:wrap;align-items:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.box .word{position:relative;margin:0px 5px 2px 5px}.box .word.wrong{-webkit-text-decoration:2px underline var(--hl-color);text-decoration:2px underline var(--hl-color)}.box .typedWord{position:absolute;top:0;left:0}.box .right{color:var(--fg-color)}.box #active{position:relative;width:-webkit-max-content;width:-moz-max-content;width:max-content}.box span.wrong{color:var(--hl-color) !important}.box #caret{transition:translate .1s ease;margin-left:-7.29165px;position:absolute;color:var(--hl-color)}.box #caret.blink{animation:blink 1.5s infinite 1s}@keyframes blink{0%,100%{opacity:1}50%{opacity:0}}
header,footer{margin:50px 0px;display:flex;width:50%;justify-content:space-between}header{font-size:21pt;font-weight:900}
